import React from "react";
import { Formik } from 'formik'
import * as yup from 'yup'
import { login } from '../../redux/auth-reducer.js'
import { connect } from 'react-redux'
import { Navigate } from "react-router-dom";
// import { Navigate } from 'react-router-dom'

const LoginForm = ({ login, isAuth }) => {
  const validationSchema = yup.object().shape({
    // name: yup.string().typeError('строка должна').required('обяз'),
    // secondName: yup.string().typeError('строка должна').required('обяз'),
    password: yup.string().typeError('строка должна').required('обяз'),
    // confirmPassword: yup.string().oneOf([yup.ref('password')], 'пароли не совпадают').required('обяз'),
    email: yup.string().email('введите').typeError('строка должна').required('обяз'),
  })
  // if (props.isAuth) {
  //   return <Navigate replace to='/profile' />
  // }
  if (isAuth) {
    return <Navigate to='/profile' />
  }
  return (
    <div>
      <Formik 
        initialValues={{
          // name: '',
          // secondName: '',
          password: '',
          email: '',
          checkbox: '',
        }}
        validateOnBlur
        onSubmit={(values, { setSubmitting, setStatus}) => {
          login(values.email, values.password, values.checkbox, setStatus)
          setSubmitting(false)
        }}
        validationSchema={validationSchema}
      >
      {({values, errors, touched, handleChange, handleBlur, isValid, handleSubmit, dirty, status}) => (
        <form onSubmit={handleSubmit}>
          <p>
            <input
              type="email"
              name='email'
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.email}
              placeholder={'эмайл'} />
          </p>
            {touched.email && errors.email && <p>{errors.email}</p>}

          <p>
            <input 
              type="password"
              name='password'
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.password}
              placeholder={'пароль'} />
          </p>
            {touched.password && errors.password && <p>{errors.password}</p>}

          <p>
            <input type="checkbox" value={values.checkbox} onChange={handleChange} name={'checkbox'}/> remember Me
          </p>
            <button disabled={!isValid && !dirty} onSubmit={handleSubmit} type='submit'>Отправить</button>
            <div>{status}</div>
        </form>
      )}
      </Formik>
    </div>
  )
}

let mapStateToProps = (state) => ({
  isAuth: state.auth.isAuth,
})

export default connect(mapStateToProps, {login})(LoginForm)