import s from './Header.module.css'
import { NavLink } from 'react-router-dom'

const Header = (props) => {
  return (
    <header className={s.header}>
      <img alt='' src='https://upload.wikimedia.org/wikipedia/commons/thumb/1/17/Yandex_Zen_Logo.png/200px-Yandex_Zen_Logo.png' />
      <div className={s.loginBlock}>
        {props.isAuth 
        ? <div>{props.login}
            <button onClick={props.logout}>Log out</button>
          </div>
        : <NavLink to='/login'>
          login
        </NavLink>}
      </div>
    </header>
  )
}

export default Header